export function getPrettyDate(date: Date): string {
  const months = [
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    ''
  ]
  const weeks = ['', '', '', '', '', '', '']

  const month = months[date.getMonth()].toLowerCase()
  const week = weeks[date.getUTCDay()]
  const day = date.getDate()

  return `${day} ${month}, ${week}`
}
